<script>
  // @ is an alias to /src
  import projectList from "@/json/project.json";
  // const projectList = projectdb

  export default {
    name: "HomeView",
    data() {
      return {
        page: "",
        links: [
          {name: "ê¹ƒí—™", src: "github.png"},
          {name: "Resume", src: "notion.png"},
          {name: "ë²¨ë¡œê·¸", src: "velog.jpeg"},
        ],
        scroll: false,
        // projectList: projectList,
      };
    },
    components: {
      // ZModal
    },
    mounted() {
      window.scrollTo(0, 0);
      document.addEventListener("scroll", this.detectScroll);
    },
    unmounted() {
      document.removeEventListener("scroll", this.detectScroll);
    },
    computed: {
      projectList() {
        return projectList.reverse();
      },
    },
    methods: {
      movePage(page) {
        let href = "";
        switch (page) {
          case "ê¹ƒí—™":
            href = "https://github.com/zyekim";
            break;
          case "Resume":
            href =
              "https://www.notion.so/zyeKim-Code-e4b2c4cd4dbf4280b4cffee22669a8cf?pvs=4";
            break;
          case "ë²¨ë¡œê·¸":
            href = "https://velog.io/@k_jihye92/posts";
            break;
        }
        return window.open(href, "_blank");
      },
      detectScroll() {
        const scrollPosition =
          window.scrollY || document.documentElement.scrollTop;

        if (scrollPosition > 0) {
          this.scroll = true;
        } else if (scrollPosition === 0) {
          this.scroll = false;
        }
      },
      moveScroll(target) {
        const pageHeaderHeight = 60;
        let pageLocation =
          document.getElementsByClassName(target)[0].offsetTop -
          pageHeaderHeight;
        window.scrollTo({top: pageLocation, behavior: "smooth"});
      },
      scrollTop() {
        window.scroll({
          top: 0,
          behavior: "smooth",
        });
      },
    },
  };
</script>
